  background: linear-gradient(30deg, var(--iai-top-nav-gradient-colour-1, var(--iai-colour-brand)) 62%, var(--iai-top-nav-gradient-colour-2, #5f043a));
  content: "";
  width: calc(100% + 1.5rem);
