.iai-top-nav__mobile-button {
  box-sizing: border-box;
  cursor: pointer;
  z-index: 10;
  background-color: #0000;
  border: 2px solid #fff;
  border-radius: 8px;
  width: 64px;
  height: 64px;
  display: none;
  position: relative;
  right: -14px;
  transform: scale(.57);
}

.iai-top-nav__mobile-button.js-init {
  display: block;
}

.iai-top-nav__mobile-button-bar {
  transform-origin: 30%;
  background-color: #fff;
  border-radius: 3px;
  width: 32px;
  height: 3px;
  animation-duration: 0s;
  animation-fill-mode: forwards;
  position: absolute;
  left: 13px;
}

@media (prefers-reduced-motion: no-preference) {
  .iai-top-nav__mobile-button--initiated .iai-top-nav__mobile-button-bar {
    animation-duration: .4s;
  }
}

.iai-top-nav__mobile-button-bar--1 {
  top: 19px;
}

[aria-expanded="true"] .iai-top-nav__mobile-button-bar--1 {
  animation-name: bar1-open;
}

[aria-expanded="false"] .iai-top-nav__mobile-button-bar--1 {
  animation-name: bar1-close;
}

.iai-top-nav__mobile-button-bar--2 {
  top: 27px;
}

[aria-expanded="true"] .iai-top-nav__mobile-button-bar--2 {
  animation-name: bar2-open;
}

[aria-expanded="false"] .iai-top-nav__mobile-button-bar--2 {
  animation-name: bar2-close;
}

.iai-top-nav__mobile-button-bar--3 {
  top: 35px;
}

[aria-expanded="true"] .iai-top-nav__mobile-button-bar--3 {
  animation-name: bar3-open;
}

[aria-expanded="false"] .iai-top-nav__mobile-button-bar--3 {
  animation-name: bar3-close;
}

@keyframes bar1-open {
  0% {
    top: 19px;
    left: 13px;
  }

  50% {
    top: 27px;
    left: 13px;
    transform: rotate(0);
  }

  100% {
    top: 23px;
    left: 15px;
    transform: rotate(45deg);
  }
}

@keyframes bar2-open {
  0% {
    opacity: 1;
  }

  49% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

@keyframes bar3-open {
  0% {
    top: 35px;
    left: 13px;
  }

  50% {
    top: 27px;
    left: 13px;
    transform: rotate(0);
  }

  100% {
    top: 32px;
    left: 15px;
    transform: rotate(-45deg);
  }
}

@keyframes bar1-close {
  0% {
    top: 23px;
    left: 15px;
    transform: rotate(45deg);
  }

  50% {
    top: 27px;
    left: 13px;
    transform: rotate(0);
  }

  100% {
    top: 19px;
    left: 13px;
  }
}

@keyframes bar2-close {
  0% {
    opacity: 0;
  }

