  outline-offset: 2px;
  border-radius: .25rem;
  outline: 2px solid #005fcc;
}

.exit-feedback__input-notes {
  font-size: .875rem;
}

.exit-feedback__send-button {
  min-height: 2.25rem;
  margin-bottom: 0;
}

.exit-feedback__confirmation-container {
  align-items: center;
  gap: .75rem;
  display: flex;
}

.exit-feedback__tick-animation {
  pointer-events: none;
  flex-shrink: 0;
  width: 3.75rem;
  height: 3.75rem;
  position: relative;
}

.exit-feedback__tick-animation > div {
  width: 10rem;
  position: absolute;
  top: -3.125rem;
  left: -3.125rem;
}

.exit-feedback__confirmation-text {
  margin-bottom: .5rem;
  font-weight: 500;
}

.exit-feedback__dismiss {
  cursor: pointer;
  background-color: #0000;
  border: 0;
  padding: 0;
  font-size: 11px;
  font-weight: 500;
}

.exit-feedback__dismiss:hover {
  text-decoration: underline;
}

.exit-feedback__toggle-button {
  cursor: pointer;
  background-color: #efdb5d;
  border: 0;
  flex-shrink: 0;
  width: 1.75rem;
  position: relative;
}

.exit-feedback__toggle-button-icon {
  width: 2rem;
  transition: transform .5s;
  position: absolute;
  top: 0;
  left: -.0625rem;
  transform: rotate(-90deg);
}

.exit-feedback__toggle-button-text {
  font-size: .75rem;
  display: block;
  position: absolute;
  top: 3.625rem;
  left: -.8125rem;
  transform: rotate(-90deg);
}

@keyframes exit-feedback-wobble {
  0% {
    transform: rotate(-90deg);
  }

  33% {
    transform: rotate(-120deg);
  }

  67% {
    transform: rotate(-60deg);
  }

  100% {
    transform: rotate(-90deg);
  }
}

.exit-feedback__toggle-button:hover .exit-feedback__toggle-button-icon {
  animation-name: exit-feedback-wobble;
  animation-duration: .5s;
}

@media (prefers-reduced-motion) {
  .exit-feedback__toggle-button:hover .exit-feedback__toggle-button-icon {
    animation-duration: 0;
  }
}

.profile {
  z-index: 100;
  background-color: #fff;
  border: 0;
  padding: 1rem 2rem;
  transition: opacity .5s, scale .7s;
}

.profile[open] {
  opacity: 1;
  scale: 1;
}

@starting-style {
  .profile[open] {
    opacity: 0;
    scale: 0;
  }
}

.profile::backdrop {
  background-color: #000c;
}

.profile--hidden {
  display: none;
}

.profile-label {
  margin-bottom: .5rem;
  display: block;
}

.profile__textarea {
  display: block;
}

.profile-nav-button-container {
  justify-content: space-between;
  display: flex;
}

@media (width >= 1020px) {
  .profile {
    width: 80%;
    height: 60%;
  }

  .profile__radio-container-inner {
    flex-direction: row;
  }
}

.profile__success-animation {
  width: 200px;
}

.experience-cards {
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 1rem;
  display: flex;
}

.experience-cards .govuk-radios__item {
  width: 11rem;
  margin-bottom: 0;
}

.experience-cards input {
  opacity: 0;
  position: absolute;
}

.experience-cards label {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #e5e7eb;
  border-radius: .25rem;
  max-width: none;
  height: 100%;
  padding: 1rem;
  font-size: 1rem;
  line-height: 1.3;
  display: block;
  position: relative;
}

.experience-cards input:focus-visible + label {
  outline: 3px solid #fd0;
}

.experience-cards label:before {
  opacity: .5;
  justify-content: center;
  width: 100%;
  height: 5.75rem;
  margin-bottom: 1.25rem;
  transition: all .5s;
  display: flex;
  transform: scale(.85);
}

.experience-cards input:checked + label {
  border-color: var(--iai-colour-text-primary);
}

.experience-cards label:hover:before, .experience-cards input:checked + label:before {
  opacity: 1;
  transform: scale(1);
}

.experience-cards input:checked + label:hover:before {
  border-width: 0;
}

.experience-cards .govuk-radios__item:first-child label:before {
  content: url("/static/icons/ai-experience-cards/Curious.svg");
}

.experience-cards .govuk-radios__item:nth-child(2) label:before {
  content: url("/static/icons/ai-experience-cards/Cautious.svg");
}

.experience-cards .govuk-radios__item:nth-child(3) label:before {
  content: url("/static/icons/ai-experience-cards/Enthusiastic.svg");
  margin-top: -.75rem;
  margin-bottom: 2rem;
}

.experience-cards .govuk-radios__item:nth-child(4) label:before {
  content: url("/static/icons/ai-experience-cards/Experienced.svg");
}

.experience-cards .govuk-radios__item:nth-child(5) label:before {
  content: url("/static/icons/ai-experience-cards/Alchemist.svg");
}

.experience-cards input:checked + label:after {
  content: url("/static/icons/ai-experience-cards/Tick.svg");
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.experience-cards .govuk-hint {
  color: var(--iai-colour-text-primary);
  margin-top: .5rem;
  margin-bottom: 0;
  padding: 0;
  font-size: .75rem;
}

.iai-chat-bubbles__sources-link:visited {
  color: var(--iai-product-colour, var(--iai-colour-brand));
}
